import { Document, Query } from '@ali/4ever-cangjie';
import RefBlock from '../../mo/models';

export const isContainer: Query = (controller, node) => {
  // 避免拷贝只含一个 void child 的引用块，粘贴时引用块丢失，只留下 void child
  if (
    Document.isDocument(node) &&
    node.nodes.length === 1 &&
    RefBlock.isRefBlock(node.nodes[0])
  ) {
    return true;
  }

  return undefined;
};
